* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

#game {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
}

#head {
  font-size: 25px;
  padding: 15px;
}

#status {
  margin-bottom: 20px;
  font-size: 20px;
  color: red;
}

#board {
  display: -ms-flexbox;
  display: flex;
  width: 300px;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.square {
  width: 100px;
  height: 100px;
  border: 1px solid #000000;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  font-size: 45px;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.square:hover {
  background: #eee;
}

.square:nth-child(1), .square:nth-child(2), .square:nth-child(3) {
  border-top: 0;
}

.square:nth-child(7), .square:nth-child(8), .square:nth-child(9) {
  border-bottom: 0;
}

.square:nth-child(3n+1) {
  border-left: 0;
}

.square:nth-child(3n) {
  border-right: 0;
}

.square.winnter {
  background: red;
}
.gameEnded .square:hover {
  background: none;
  cursor: default;
}


